.input{
    background-image: linear-gradient(rgba(250, 250, 250, 0.5), rgba(250, 250, 250, 0.5)),
                    linear-gradient(to right bottom, red,orange,yellow);
    color: rgba(0,0,0,0.8);
    border-width: 1.5px;
    border-color: rgba(112, 52, 3, 0.9);
}

.input:focus{
    background-image: linear-gradient(rgba(250, 250, 250, 0.2), rgba(250, 250, 250, 0.2)),
                    linear-gradient(to right bottom, red,orange,yellow);
    box-shadow: none;
    border-color: rgba(112, 52, 3, 0.7);
    color: rgba(0,0,0,1);
}

.input::placeholder{
    font-family: 'Raleway', sans-serif;
    font-size: 0.8rem;
    color: rgba(55,55,55,0.5);
}

.dark-input{
    background-image: linear-gradient(rgba(150, 150, 150, 0.4), rgba(150, 150, 250, 0.4)),
                    linear-gradient(to right bottom, red,orange,yellow);
    color: rgba(250,250,250,1);
}

.dark-input:focus{
    background-image: linear-gradient(rgba(150, 150, 150, 0.2), rgba(150, 150, 250, 0.2)),
                    linear-gradient(to right bottom, red,orange,yellow);
    color: rgba(250,250,250,1);
}

.dark-input::placeholder{
    color: rgba(250,250,250,0.6);
}

.button{
    font-family: 'Raleway', sans-serif;
    font-size: 0.9rem;
    color: rgba(55,55,55,0.5);
    background-image: linear-gradient(rgba(250, 250, 250, 0.2), rgba(250, 250, 250, 0.4)),
                    linear-gradient(to right bottom, red,orange,yellow);
    border: 1.5px solid rgba(112, 52, 3, 0.7);
}

.button:focus{
    box-shadow: none;
}

.button:active{
    border-color: rgba(250,250,250,0.9);
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
                    linear-gradient(to right bottom, red,orange,yellow);
    color: rgba(250,250,250,0.6);
}

.dark-button{
    border-color: rgba(25,25,25,0.9);
}

.dark-button:hover{
    background-image: linear-gradient(to right bottom, red,orange,yellow);
}

.dark-button:active{
    border-color: rgba(250,250,250,0.9);
}

